import React from 'react';
import '../CSS/TopNavmenu.css'

function TopnavMenu (){
    return (
      <div className="TopnavMenu">
            {/* LOCATION FIELD STARTS HERE */}
            <div className="top-location">
              <img src={require("../Images/location.svg")} alt="location" />
              <p>Malegaon MIDC Sinnar nashik maharashtra.</p>
            </div>
            {/* LOCATION FIELD ENDS HERE */}
            {/* SEARCH FIELD STARTS HERE */}
            <div className="SearchField">
              <div className="searchIcon" />
              <input type="search" className="search" name placeholder="Search anything what you want.." />
            </div>
            {/* SEARCH FIELD ENDS HERE */}
      </div>
    );
  }

export default TopnavMenu;
