import React from 'react';
import '../CSS/Account.css';
import LandingProducts from './LandingProducts'

function Account (){
	return (


      <div>
      <nav class="m-top-nav">
        <div class="return"></div>
        <div class="m-top-nav-title">my account</div>
      </nav>
      <div className="user-detail">
        <div className="nature-back" style={{height: '30vh', width: '100%', background: 'url(../Images/nature.jpg)', backgroundRepeat: 'no-repeat', backgroundPosition: 'center', backgroundSize: 'cover'}} />
        <div className="profile-image">
          <img src={require("../Images/profile.jpg")} alt="" className="pp" />
        </div>	
        <h1 className="user-name">Eliyana William </h1>
        <div className="edit-option">
          <div className="edit-profile">
            <h2 className="edit-p-text">Edit Prorile</h2><img src={require("../Images/pencil.png")} alt="" className="edit-profile" />
          </div>
          <div className="option">
            <div className="icons" />
          </div>
        </div>
        {/* DETAILS STARTS HERE */}
        <div className="information">
          <div className="town"><img src={require("../Images/city.png")} alt="" />Home Town: <span className="home-town">MIDC Malegaon Sinnar</span></div>
          <div className="joined"><img src={require("../Images/join.png")} alt="" />Joined On: <span className="joined-date">5 Jan 2020</span></div>
          <div className="contact"><img src={require("../Images/phone.png")} alt="" />Contact: <span className="Contact-no">+91 7709543082</span></div>
        </div>
        {/* DETAILS ENDS HERE */}
        {/* AD AND WISHES TAB STARTS HERE */}
        <div className="ads-wish">
          <div className="myads">My Ads</div>
          <div className="mywishes">My Wishlist </div>
        </div>
        {/* AND AND WISHES TAB ENDS HERE */}
        {/* ADDED CONTENTS ARE REFLECTED UNDER HERE HERE */}
        {/* ADDED CONTENTS ARE REFLECTED UPPER HERE HERE */}
      </div>
      <LandingProducts />
      </div>
    );
}

export default Account;