import React from 'react';
import '../CSS/LowerNav.css';
import {Link} from 'react-router-dom';


function LowerNavmenu() {
    return (
         

          <div className="m-nav">
          <Link to="/" className="">
          <div className="nav-menu home">
              <img src={require("../Images/home.svg")} alt="Home" />
              <div className="explore-text">Explore</div>
          </div>
          </Link>
         <div className="nav-menu catogery">
              <img src={require("../Images/catogery.svg")} alt="catogery" />
              <div className="explore-text">Catogery</div>
         </div>
        <div className="nav-menu sell">
          <img src={require("../Images/sell.svg")} alt="Sell" />
          <div className="explore-text">Sell</div>
        </div>
        <div className="nav-menu chat">
          <img src={require("../Images/chat.svg")} alt="Chat" />
          <div className="explore-text">Chats</div>
        </div>

      
        
        <Link to="./Account">
        <div  className="nav-menu account">
          <img src={require("../Images/account.svg")} alt="Account" />
          <div className="explore-text">Account</div>
        </div></Link>

          </div>
    );
  }

export default LowerNavmenu;
